import React, { useState } from 'react';
import { useRouter } from 'next/router';
import Navbar from '../../components/Navbar';

const HolderIndexPage = () => {
  const [walletAddress, setWalletAddress] = useState('');
  const router = useRouter();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (walletAddress) {
      router.push(`/holder/${walletAddress}`);
    }
  };

  return (
    <>
      <Navbar />
      <div className="container mx-auto p-4">
        <h1 className="text-3xl font-bold mb-4">Holder Page</h1>
        <p>Welcome to the holder page. Please enter a wallet address to view NFTs.</p>
        <form onSubmit={handleSubmit} className="mt-4">
          <input
            type="text"
            value={walletAddress}
            onChange={(e) => setWalletAddress(e.target.value)}
            placeholder="Enter wallet address"
            className="p-2 border rounded w-full"
          />
          <button
            type="submit"
            className="mt-2 px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-700"
          >
            View NFTs
          </button>
        </form>
      </div>
    </>
  );
};

export default HolderIndexPage;